body {
  background-color: #ededed;
}
body #root {
  font-family: FSThriveElliotWeb-Light, "Helvetica Neue", Helvetica, Arial, sans-serif;
}

img {
  max-width: 100%;
  display: block;
}

a {
  -webkit-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
  text-decoration: none;
  color: #2ec4f1;
  outline: none;
}
a:hover {
  color: #8edff7;
}

main {
  margin-top: 43px;
}

.container {
  width: 1200px;
  margin: auto;
  padding: 0;
}

header {
  -webkit-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
  color: #fff;
  background-color: rgba(5, 26, 52, 0.8);
  text-align: center;
  padding: 12px 4px;
}
header.active {
  -webkit-box-shadow: 0 -16px 18px 12px #000;
          box-shadow: 0 -16px 18px 12px #000;
}
header > div {
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.App-banner {
  overflow: hidden;
  background-color: #d1d0d7;
}
.App-banner h1 {
  font-size: 3em;
  line-height: 1em;
}
.App-banner p {
  font-size: 1.3em;
  margin: 8px 0;
}
.App-banner .button {
  margin: 22px 0;
  font-size: 1.2em;
  color: #fff;
  /* usage @import border-radius(12px) */
  border-radius: 20px;
  background-clip: padding-box;
  /* stops bg color from leaking outside the border: */
  padding: 10px 3em;
  display: inline-block;
}

.Card {
  -webkit-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
  cursor: pointer;
  min-height: 80px;
  margin: 8px 0;
  padding: 18px;
  background-color: #fff;
  border: solid 1px #d1d2d4;
  /* usage @import border-radius(12px) */
  border-radius: 8px;
  background-clip: padding-box;
  /* stops bg color from leaking outside the border: */
}
.Card.active {
  -webkit-box-shadow: 0 0 10px -4px #000;
  box-shadow: 0 0 10px -4px #000;
}

.panel {
  max-height: 0px;
  overflow: hidden;
  -webkit-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
}
.panel p {
  font-size: 2em;
}
.panel.visible {
  max-height: 300px;
}

nav li {
  display: inline;
  margin: 2em;
}
nav li a {
  color: #fff;
}

footer {
  background-color: #2f4258;
  text-align: center;
  padding: 12px 8px;
}
footer > div {
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}



